::-webkit-scrollbar {
  width: 5px;
  color: #ff00ff;
  background-color: #ff7f00;
}

::-webkit-scrollbar-track {
  background: rgb(255, 244, 244);
}

::-webkit-scrollbar-thumb {
  border-radius: 120px;
  background: gray;
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(17, 0, 255);
  color: #ff00ff;
}
/* /// */

.socel-media-icons {
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

code:not(pre code) {
  background: #f7f7f7;
  border: 1px solid #ededed;
  border-radius: 7px !important;
}

.mainPostContainer a {
  color: #38bdf8 !important;
}

/* Dark Mode Scrollbar Styles */
@media (prefers-color-scheme: dark) {
  ::-webkit-scrollbar-track {
    background: rgb(0, 0, 0);
  }
  ::-webkit-scrollbar-thumb {
    background: #555;
  }

  code:not(pre code) {
    background: #2a2828;
    border: 1px solid #3e3c3c;
    border-radius: 7px !important;
  }
}

/* Code Block Styles */
.prose pre {
  padding: 16px !important;
  margin: 0 !important;
  filter: drop-shadow(0 1px 1px rgb(0 0 0 / 0.05)) !important;
  margin: 16px 0px !important;
} 

.prose pre:hover {
  filter: drop-shadow(0 0 0.75rem rgb(20, 220, 137)) !important;
}

.prose pre code {
  color: rgb(255, 249, 240) !important;
}

/* Article Content Styles */
.myPostContentInArticle p {
  color: #414040 !important;
  font-size: 20px !important;
}

.myPostContentInArticle img {
  display: block !important;
  margin: auto !important;
  border-radius: 8px !important;
  cursor: pointer;
}

.myPostContentInArticle iframe {
  display: block;
  margin: auto;
  border-radius: 8px;
  max-width: 100%;
  /* height: 100%; */
}


/* styles.css */

/* Add styles for the clipboard button */
.copy-button {
  position: relative;
  cursor: pointer;
}

.copy-icon {
  fill: #333; /* Color of the clipboard icon */
  width: 20px; /* Adjust the width as needed */
  height: 20px; /* Adjust the height as needed */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Optional: Add styles for a tooltip when hovering over the button */
.copy-button:hover::after {
  content: 'Copy to Clipboard';
  position: absolute;
  top: -25px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #333;
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  font-size: 12px;
  opacity: 0.8;
  pointer-events: none;
}












/* Dark Mode Article Content Styles */
@media (prefers-color-scheme: dark) {
  .myPostContentInArticle h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: ghostwhite !important;
    font-weight: 700 !important;
    line-height: 1 !important;
  }

  .myPostContentInArticle p {
    color: #cbd5e1 !important;
    font-size: 20px !important;
  }
}

/* Responsive Styles */
@media (max-width: 420px) {
  .highlightsMainDiv .highlights {
    display: flex;
    flex-direction: column;
  }
  .highlights .contants {
    width: 100% !important;
  }
}

@media (540px <= width <=640px) {
  .myPostContentInArticle iframe {
    max-width: 500px !important;
    max-height: 280px !important;
  }
}
/* max-width=500px */
@media (0px <= width <=500px) {
  .mainContentsPost {
    padding: 0px !important;
  }

  .mainContentsPost .mytitle {
    font-size: 14px !important;
  }

  .mainContentsPost .mydescription {
    font-size: 13px !important;
  }
  .highlights img {
    width: 5rem;
  }
}

@media (0px <= width <=640px) {
  .myPostContentInArticle p {
    font-size: 16px !important;
  }
  /* .myPostContentInArticle iframe {
    max-width: 300px !important;
    max-height: 140px !important;
  } */
}

@media (0px <= width <=768px) {
  .highlights {
    font-size: 13px;
  }
}

@media (min-width: 500px) {
}

@media (min-width: 640px) {
  .myPostContentInArticle img {
    width: 500px !important;
  }

  .myPostContentInArticle p {
